        display: flex;
        flex-flow: row wrap;
        justify-content: center;

        .banner-item {
            font-size: 2em;
            font-weight: 800;
            padding: 5px;
        }

        .name {
            color: $pink;
            padding-right: 20px;
        }

        .role {
            color: #fff;
            padding-left: 20px;
            box-sizing: border-box;
