    // @media only screen and (max-width: $xlDevice) {}

    // @media only screen and (max-width: $lgDevice) {}

    @media only screen and (max-width: $mdDevice) {}

    @media only screen and (max-width: $smDevice) {

        .text-container {
            padding-left: 20px;

            .banner-item {
                font-size: 2em;
            }

