@keyframes glitch-p {
    17% {
        --scale: .87;
    }

    31% {
        --scale: 1.1;
    }

    37% {
        --scale: 1.3;
    }

    47% {
        --scale: .91;
    }

    87% {
        --scale: 1;
    }
}

@keyframes glitch-a {

    10%,
    30%,
    50%,
    70%,
    90% {
        --top: 0;
        --left: 0.5;
    }

    0% {
        --v-height: 15%;
    }

    20% {
        --left: .005;
    }

    40% {
        --left: .01;
        --v-height: 20%;
        --top: 3;
    }

    60% {
        --left: .03;
        --v-height: 25%;
        --top: 6;
    }

    80% {
        --left: .07;
        --v-height: 5%;
        --top: 8;
    }

    100% {
        --left: .083;
        --v-height: 30%;
        --top: 1;
    }
}

@keyframes glitch-b {

    10%,
    30%,
    50%,
    70%,
    90% {
        --top: 0;
        --left: 0;
    }

    0% {
        --v-height: 15%;
        --top: 10;
    }

    20% {
        --left: -.005;
    }

    40% {
        --left: -.01;
        --v-height: 17%;
        --top: 3;
    }

    60% {
        --left: -.03;
        --v-height: 35%;
        --top: 6;
    }

    80% {
        --left: -.07;
        --v-height: 5%;
        --top: 8;
    }

    100% {
        --left: -.083;
        --v-height: 30%;
        --top: 1;
    }
}